.favorites {

  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: fixed;
    top: 0;
    left: 0;
    background: var(--light-color);
    width: 100%;
    z-index: 10;
    padding: 20px;
  }

  &__title {
    font-size: 18px;
    font-weight: 700;
    line-height: 28px;
    min-height: 28px;
    color: var(--dark-color);
    margin: 0;
  }

  &__content {
    position: relative;
    display: flex;
    align-items: center;
    gap: 10px;
  }

  &__btn{
    color: var(--accent-color-2);
    font-size: 16px;
    font-weight: 400;
  }
  &__menu {
    position: absolute;
    top: 0;
    right: 28px;
    border-radius: 5px;
    z-index: 12;
    background: var(--light-color);
    min-width: 204px;
    padding: 5px 16px;
    box-shadow: rgba(0, 0, 0, 0.08) 0px 2px 2px 0px, rgba(0, 0, 0, 0.04) 0px 0px 2px 0px;
  }

  &__menu-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 11;
  }

  &__link {
    position: relative;
    z-index: 13;
    display: block;
    padding: 8px 0;
    color: var(--dark-color);
    font-size: 16px;
    font-weight: 500;
  }

  .tabs {

    &__nav {
      position: fixed;
      top: 65px;
      left: 0;
      width: 100%;
      display: flex;
      align-items: center;
      gap: 25px;
      min-height: 30px;
      border-bottom: 3px solid #E3E2E1;
      padding-bottom: 15px;
      padding-top: 10px;
      background: var(--light-color);
      z-index: 10;
      padding-left: 20px;
      padding-right: 20px;
    }

    &__nav-item {
      position: relative;
    }

    &__nav-btn {
      font-size: 18px;
      font-weight: 700;
      color: var(--grey-color);
    }

    &__content{
      padding-top: 140px;
    }
  }
}

.favorites-header-more {
  rotate: 90deg;
}
.favorites-ads {

  &__list {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 10px;
    margin-bottom: 20px;
  }

  &__item {
    padding: 6px 15px;
    border-radius: 6px;
    background: #EBEBEB;
  }

  &__btn {
    color: var(--dark-color);
    font-size: 16px;
    font-weight: 500;
  }

  &__filter {
    display: flex;
    align-items: center;
    gap: 5px;
    font-size: 13px;
    color: var(--dark-color);
    margin-bottom: 15px;
  }

  &__market {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 25px;

    .product{
      box-shadow: unset !important;
    }
    .product__picture .image{
      border-radius: 5px;
    }
  }
}
.favorites-selection {

  &__list {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 20px;
  }

  &__item {
    display: flex;
    align-items: flex-start;
    gap: 10px;

    .image {
      border-radius: 5px;
    }
  }

  &__content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
  }

  &__title {
    font-size: 16px;
    font-weight: 700;
    line-height: 22px;
  }

  &__num {
    font-size: 13px;
    font-weight: 400;
    line-height: 22px;
    margin-top: 5px;
  }

  &__content-list {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
.favorites-search {

  &__list {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 20px;
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    background: var(--light-color);
    border-radius: 5px;
    padding: 15px;
    box-shadow:
        0 2px 2px 0 rgba(0, 0, 0, 0.08),
        0 0 2px 0 rgba(0, 0, 0, 0.04);
  }

  a{
    display: block;
    width: 100%;
  }

  &__title {
    font-size: 16px;
    font-weight: 700;
    color: var(--dark-color);
    line-height: 30px;
  }

  &__path {
    font-size: 14px;
    font-weight: 400;
    color: var(--grey-color);
    line-height: 30px;
  }
}
.favorites-compare {

  &__list {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 20px;
  }

  &__item {

    a{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }

  &__title {
    color: var(--dark-color);
    font-size: 16px;
    font-weight: 500;
    line-height: 22px;
  }

  &__num {
    color: var(--grey-color);
    font-size: 15px;
    font-weight: 400;
    line-height: 22px;
  }

  &__pictures {
    position: relative;

    .image{
      border-radius: 5px;
      border-right: 2px solid var(--light-color);

      &:nth-child(1){
        position: absolute;
        right: 30px;
      }
    }
  }
}
.favorites-profile {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 20px;

  &__user {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-bottom: 15px;

    .image{
      border-radius: 50%;
    }
  }

  &__name {
    color: var(--dark-color);
    font-size: 16px;
    font-weight: 700;
    line-height: 22px;
  }

  &__num {
    color: var(--grey-color);
    font-size: 13px;
    font-weight: 400;
    line-height: 18px;
  }

  &__list {
    display: flex;
    align-items: stretch;
    gap: 20px;
    padding-bottom: 10px;
  }

  &__item {
    min-width: 150px;
    background: var(--light-color);
    box-shadow:
        0 2px 2px 0 rgba(0, 0, 0, 0.08),
        0 0 2px 0 rgba(0, 0, 0, 0.04);
  }

  &__list-content {
    display: flex;
    align-items: flex-start;
    padding: 5px;
    gap: 20px;
  }

  &__list-title {
    font-size: 15px;
    font-weight: 400;
    line-height: 22px;
    color: var(--dark-color);
  }

  &__list-price {
    font-size: 15px;
    font-weight: 700;
    line-height: 22px;
    color: var(--dark-color);
  }

  &__btn {
    min-width: 18px;
    min-height: 16px;
  }
}

.favorites-ads-active-item{
  background: #CFEDFF;
}

.favorites-body{
  padding-bottom: 55px;
}
