.card {
    position: relative;
    z-index: 1;
    box-shadow:
        0 2px 2px 0 rgba(0, 0, 0, 0.08),
        0 0 2px 0 rgba(0, 0, 0, 0.04);
    background: var(--light-color);
    border-radius: 3px;
    overflow: hidden;
    &__title {
        margin: 0;
        border: none;
        text-align: left;
        font-size: 14px;
        font-weight: 400;
        color: var(--dark-color);
        line-height: 16px;
        overflow: hidden;
        text-overflow: ellipsis;
    }
    &__info {
        display: flex;
        align-items: stretch;
        padding: 5px;
    }
    &__price {
        display: block;
        color: var(--dark-color);
        font-size: 14px;
        font-weight: 400;
        line-height: 22px;
        font-weight: 700;
        margin-bottom: 3px;
    }
    &__location {
        display: block;
        color: rgb(133, 133, 133);
        font-size: 13px;
        line-height: 18px;
        margin-bottom: 3px;
    }
    &__date {
        display: block;
        color: rgb(133, 133, 133);
        font-size: 13px;
        line-height: 18px;
    }
    &__menu {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 15px;
        min-width: 36px;
    }
    &__menu-btn {
        position: relative;
        z-index: 3;
    }
}

.catalog {
    &__inner {
        padding-top: 12px;
    }
    &__title {
        margin: 0 0 0 16px;
        line-height: 1.3;
        font-size: 22px;
        font-weight: 600;
        width: calc(100% - 16px - 0px);
    }
    &__list {
        overflow-x: scroll;
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        align-items: stretch;
        gap: 10px;
        padding-top: 8px;

        &::-webkit-scrollbar{
            display: none;
        }
    }

    &__more{
        display: block;
        padding: 12px 16px;
        border-radius: 6px;
        margin-top: 16px;
        background: #F2EFE9;
        font-size: 16px;
        line-height: 20px;
        color: var(--dark-color);
        width: fit-content;
    }
}

.more {
    padding: 16px 18px;

    &__title {
        display: block;
        color: var(--dark-color);
        font-size: 22px;
        font-weight: 700;
        line-height: 26px;
        margin: 0;
    }
    &__list {
        display: flex;
        flex-direction: column;
    }
    &__text {
        color: var(--dark-color);
        font-size: 16px;
        line-height: 24px;
        margin-top: 12px;
        margin-bottom: 0;
    }
}


@media (max-width: 840px) {
    .catalog__list {
        grid-template-columns: repeat(3, 1fr);
    }
}

@media (max-width: 600px) {
    .catalog__list {
        grid-template-columns: repeat(2, 1fr);
    }
}

.special-catalog-list{
    display: flex !important;

    & .card{
        min-width: 240px;
    }
}