$colHeight: 80px;

.hero {
    &__inner {
        padding-top: 75px;
    }
    &__list {
        display: flex;
        align-items: center;
        margin-bottom: 15px;
    }
    &__item{
        min-width: fit-content;

        &:not(:last-child){
            margin-right: 17px;
        }
    }
    &__link {
        display: flex;
        align-items: center;
        gap: 5px;
        color: var(--dark-color);
        font-size: 13px;
        white-space: nowrap;
    }
}
.menu {
    display: flex;
    align-items: stretch;
    gap: 5px;

    &__list {
        display: grid;
        grid-template-columns: repeat(19, [ col ] 42px);
        grid-template-rows: repeat(2, [ row ] $colHeight);
        gap: 5px;
        align-items: stretch;
        width: 100%;

        &:not(:last-child){
            margin-bottom: 5px;
        }
    }
    &__item {
        background: var(--accent-color);
        border-radius: 16px;
        width: 100%;
        background-size: cover;
        background-repeat: no-repeat;
        background-position-x: 100%;
    }
    &__link {
        display: block;
        min-width: 100%;
        min-height: 100%;
        padding: 12px 14px;
    }
    &__title {
        display: block;
        font-size: 13px;
        line-height: 16px;
        color: #000;
        margin: 0;
        hyphens: auto;
    }
    &__link-all {
        display: flex;
        align-items: center;
        justify-content: center;
        background: var(--accent-color);
        height: calc($colHeight * 2);
        border-radius: 16px;
        min-width: 120px;
        max-width: 120px;
        padding: 10px 14px;
        font-size: 13px;
        color: #000;
        text-align: center;

        & span{
            display: inline;
        }

        & svg{
            padding-left: 4px;
        }
    }
}
.category-auto {
    grid-column: span 3;
    background-image: url("../img/category-1.png");
}
.category-immovables {
    grid-column: span 3;
    background-image: url("../img/category-2.png");

    & .menu__title{
        max-width: 80px;
    }
}
