.user {
  background: var(--light-color);
  box-shadow: rgba(0, 0, 0, 0.08) 0px 2px 2px 0px;

  &__inner {
    padding: 10px 0;
  }

  &__content {
    display: flex;
    align-items: stretch;
    gap: 10px;
  }

  &__content-link {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;

    & .image{
      border-radius: 50%;
    }
  }

  &__content-item {
    width: 72px;
    height: 72px;
    border-radius: 50%;
  }

  &__content-btn {
    background: var(--accent-color);
  }

  &__title {
    margin: 5px 0 8px;
    color: var(--dark-color);
    font-size: 22px;
    font-weight: 500;
    line-height: 28px;
  }

  &__text {
    display: block;
    color: var(--dark-color);
    font-size: 17px;
    font-weight: 400;
    line-height: 24px;
  }

  &__rating {
    display: flex;
    align-items: center;
    gap: 5px;
    margin-top: 8px;
  }

  &__rating-item{
    fill: #E0E0E0;
  }

  &__rating-num {
    display: block;
    font-size: 16px;
    font-weight: 700;
    color: var(--dark-color);
  }

  &__link {
    display: block;
    margin-top: 5px;
    color: var(--accent-color-2);
    font-size: 16px;
    font-weight: 400;
  }
}
.user-current-profile {

  &::after{
    content: '';
    position: absolute;
    top: 50%;
    right: 50%;
    transform: translate(50%, -50%);
    width: 72px;
    height: 72px;
    border: 4px solid var(--accent-color-2);
    border-radius: 50%;
  }
}
