.tabs__panel {
    display: none;
}
.tabs__nav {
    list-style: none;
    display: flex;
    margin: 0;
    padding: 0;
}
.tabs__panel--active {
    display: block;
}
.tabs__nav-btn:focus {
}
.tabs__nav-btn--active {
    color: var(--dark-color) !important;

    &::after{
        content: '';
        position: absolute;
        bottom: -18px;
        left: 0;
        width: 100%;
        height: 3px;
        background: var(--dark-color);
    }
}
