.store {

    &__price {
        margin: 20px 0 0;
        color: var(--dark-color);
        font-size: 32px;
        font-weight: 700;
        line-height: 36px;
    }

    & .swiper{
        height: 360px !important;
        overflow: hidden;
        border-radius: 16px;
        max-width: calc(100% - 30px);

        & .image{
            border-radius: 5px;
        }
    }

    &__title {
        margin: 0;
        color: var(--dark-color);
        font-size: 18px;
        font-weight: 700;
        line-height: 22px;
    }

    &__rating {
        margin-top: 15px;
    }

    &__rating-value {
        padding: 1px 6px;
        border-radius: 3px;
        background: #00C257;
        color: var(--light-color);
        font-size: 16px;
        font-weight: 400;
    }

    &__rating-text {
        color: #00C257;
        font-size: 16px;
        line-height: 22px;
        font-weight: 400;
    }

    .description-text{
        overflow: hidden;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 4; /* Ограничение по количеству строк */
    }

    &__nav {
        display: grid;
        grid-template-columns: 60% 38%;
        align-items: stretch;
        gap: 10px;
        margin-top: 30px;
    }

    &__nav-btn {
        display: flex;
        align-items: center;
        justify-content: center;
        background: #965EEB;
        border-radius: 16px;
        min-height: 52px;
        color: var(--light-color);
        font-size: 16px;
        font-weight: 400;
    }

	&__tab {
        display: grid;
        grid-template-columns: 60% calc(40% - 5px);
        gap: 5px;
        position: fixed;
        bottom: 5px;
        right: 50%;
        transform: translateX(50%);
        width: calc(100% - 20px);
        background: var(--light-color);
        border-radius: 22px 22px 0 0;
        padding: 8px 8px 15px;
        transition: .2s linear;
	}

    .hidden-bottom{
        bottom: -100px !important;
    }

	&__tab-btn {
        display: flex;
        align-items: center;
        justify-content: center;
        color: var(--light-color);
        font-size: 15px;
        font-weight: 400;
        background: #965EEB;
        border-radius: 16px;
        height: 52px;
	}

    &__delivery {
        margin-top: 20px;
        width: 90%;
    }

    &__text {
        display: flex;
        align-items: center;
        gap: 5px;
        margin: 0;
        color: var(--dark-color);
        font-size: 16px;
        font-weight: 400;
        line-height: 22px;
    }

    &__text-delivery{
        margin: 0;
        color: var(--dark-color);
        font-size: 16px;
        font-weight: 400;
        line-height: 22px;
    }

    &__link {
        display: block;
        color: var(--accent-color-2);
        font-size: 16px;
        font-weight: 400;
        line-height: 22px;
    }

    &__user {
        margin-top: 30px;
    }

    &__user-title {
        margin: 0;
        color: var(--dark-color);
        font-size: 26px;
        font-weight: 700;
        line-height: 30px;
    }

    &__user-span {
        display: flex;
        align-items: center;
        gap: 5px;
        line-height: 30px;
    }

    &__user-star {
        display: flex;
        align-items: center;
    }

    &__user-item {
        width: 14px;
        height: 14px;
        fill: #FFB020;
    }

    &__contact {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        align-items: stretch;
        gap: 15px;
        margin-top: 10px;
    }

    &__btn {
        display: flex;
        align-items: center;
        justify-content: center;
        min-height: 44px;
        padding: 8px 16px;
        border-radius: 12px;
        color: var(--dark-color);
        font-size: 16px;
        font-weight: 400;
        background: var(--accent-color);
    }

    &__address {
        margin-top: 30px;
    }

    &__text-fragment {
        display: flex;
        align-items: center;
        gap: 5px;
        color: var(--grey-color);
    }

    &__text-walk {
        width: 30px;
        height: 30px;
        fill: var(--grey-color);
    }

    &__property {
        margin-top: 30px;
    }

    &__property-title {
        margin: 0 0 20px;
        color: var(--dark-color);
        font-size: 26px;
        font-weight: 700;
        line-height: 30px;
    }

    &__description {
        margin-top: 30px;
    }

    &__description-title {
        margin: 0 0 20px;
        color: var(--dark-color);
        font-size: 26px;
        font-weight: 700;
        line-height: 30px;
    }

    &__seller {
        margin-top: 30px;
    }

    &__seller-title {
        margin: 0;
        color: var(--dark-color);
        font-size: 26px;
        font-weight: 700;
        line-height: 30px;
    }

    &__seller-message {
        position: relative;
        margin-top: 15px;
        height: 44px;
    }

    &__seller-input {
        width: 100%;
        height: 22px;
        padding: 10px 12px 12px 16px;
        background: var(--accent-color);
        border-radius: 16px;
        height: 44px;
    }

    &__seller-btn {
        position: absolute;
        top: 50%;
        right: 15px;
        transform: translateY(-50%);
    }

    &__message-send {
        width: 20px;
        height: 22px;
        fill: #C2C1BE;
    }

    &__seller-list {
        display: flex;
        align-items: center;
        gap: 5px;
        flex-wrap: wrap;
        margin-top: 10px;
    }

    &__list-btn{
        padding: 8px 10px;
        border-radius: 12px;
        background: #292827;
        color: var(--light-color);
        font-size: 13px;
        font-weight: 400;
    }

    &__details {
        margin-bottom: 15px;

        &[open]{
            & summary::after{
                transform: rotate(0deg);
                transition: .2s linear;
            }
        }
    }

    &__summary {
        position: relative;
        list-style: none;
        color: var(--dark-color);
        font-size: 18px;
        font-weight: 700;
        width: fit-content;

        &::after{
            content: '';
            position: absolute;
            top: 2px;
            right: -20px;
            width: 20px;
            height: 20px;
            background-image: url(../img/arrow_2.svg);
            background-size: cover;
            background-repeat: no-repeat;
            transform: rotate(180deg);
            transition: .2s linear;
        }
    }

    &__list {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 8px;
        padding-top: 10px;
    }

    &__item{
        border-radius: 5px;
        overflow: hidden;
        background: var(--light-color);
        box-shadow: rgba(0, 0, 0, 0.08) 0px 2px 2px 0px, rgba(0, 0, 0, 0.04) 0px 0px 2px 0px;
    }

    &__item-description {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        padding: 5px;
    }

    &__item-title {
        display: block;
        color: var(--dark-color);
        font-size: 14px;
        line-height: 22px;
        font-weight: 400;
    }

    &__item-price {
        display: block;
        color: var(--dark-color);
        font-size: 14px;
        line-height: 22px;
        font-weight: 600;
    }

    &__report {
        height: 44px;
        width: 100%;
        padding: 6px 16px;
        background: var(--accent-color);
        border-radius: 12px;
        font-size: 16px;
        font-weight: 400;
        color: var(--dark-color);
    }

    &__info {
        margin-top: 20px;

        & span{
            display: block;
            color: rgb(117, 117, 117);
            font-size: 13px;
            font-weight: 400;
            line-height: 18px;
        }
    }

    &__info-path {
        margin-top: 30px;
    }
}
.fragment {
    color: #00C257;
}
.salesman {
    margin-top: 30px;

    &__description {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        margin-bottom: 30px;

        & .image{
            border-radius: 50%;
        }
    }

    &__title {
        margin: 0;
        color: var(--dark-color);
        font-size: 26px;
        font-weight: 700;
        line-height: 30px;
    }

    &__type {
        color: var(--dark-color);
        font-size: 16px;
        font-weight: 400;
        line-height: 22px;
    }

    &__span {
        display: block;
        padding: 2px 5px;
        background: #CCECFF;
        font-size: 13px;
        font-weight: 300;
        border-radius: 6px;
        margin-top: 10px;
        width: fit-content;
    }
}
