.retailer {
  &__inner {
    padding: 0 10px 150px;
  }

  &__company {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;

    .image {
      border-radius: 50%;
      min-width: 84px;
      min-height: 84px;
      object-fit: contain;
    }
  }

  &__company-content {
    margin-bottom: 20px;
  }

  &__title {
    color: var(--dark-color);
    font-size: 21px;
    font-weight: 700;
    line-height: 26px;
    margin: 0;
  }

  &__rating {
    display: flex;
    align-items: center;
    gap: 5px;
    margin-top: 10px;
  }

  &__rating-num {
    display: block;
    color: var(--dark-color);
    font-size: 14px;
    font-weight: 700;
    line-height: 20px;
  }

  &__star {
    display: flex;
    align-items: center;
  }

  &__star-item {
    width: 17px;
    height: 17px;
    fill: #ffb020;
  }

  &__link {
    display: flex;
    align-items: center;
    font-size: 14px;
    font-weight: 400;
    line-height: 22px;
    color: var(--accent-color-2);
  }

  &__subscribers {
    display: block;
    font-size: 15px;
    color: var(--dark-color);
    font-weight: 400;
    line-height: 22px;
  }

  &__date {
    display: block;
    font-size: 15px;
    color: var(--dark-color);
    font-weight: 400;
    line-height: 22px;
  }

  &__dashboard {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(4, 46px);
    gap: 2px;
  }

  &__dashboard-btn {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 10px;
    color: var(--dark-color);
    font-size: 13px;
    font-weight: 400;
    padding: 13px;
    background-color: #e6f6ff;
    border-radius: 6px 6px 0 0;

    &:focus {
      background: #ccecff;
    }

    &:nth-child(2) {
      border-radius: 0 0 0 0;
    }

    &:nth-child(3) {
      border-radius: 0 0 6px 6px;
    }
  }

  &__menu {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 52px;
    padding-top: 10px;
    gap: 15px;
    margin-bottom: 40px;
  }

  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--accent-color);
    border-radius: 16px;
    color: var(--dark-color);
    font-size: 16px;
    font-weight: 400;
  }

  &__tabs-nav {
    display: flex;
    align-items: center;
    gap: 30px;
    margin-bottom: 30px;
  }

  &__tabs-btn {
    position: relative;
    color: var(--grey-color);
    font-size: 21px;
    font-weight: 700;
    line-height: 26px;
  }

  &__tabs-num {
    position: absolute;
    top: -5px;
    color: var(--grey-color);
    font-size: 14px;
    text-align: left;
  }

  &__search {
    display: flex;
    align-items: center;
    gap: 15px;
    background: var(--accent-color);
    height: 52px;
    padding: 15px;
    border-radius: 16px;
    margin-bottom: 10px;
  }

  &__input {
    background: var(--accent-color);
    height: 100%;
    width: 100%;
    padding-left: 30px;
    background-image: url(../img/search.svg);
    background-repeat: no-repeat;
    font-size: 13px;
    font-weight: 400;
    color: var(--dark-color);
  }

  &__catalog-btn {
    padding: 15px 17px;
    background: var(--accent-color);
    border-radius: 12px;
    margin-bottom: 20px;
  }

  &__list {
    margin-bottom: 20px;
  }

  &__main{
    padding-top: 80px;
  }

  &__info-title {
    display: block;
    font-size: 21px;
    font-weight: 700;
    line-height: 26px;
    color: var(--dark-color);
    margin-bottom: 15px;
  }

  &__info-slider {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-bottom: 30px;

    & .image {
      height: 158px;
      border-radius: 8px;
    }
  }

  &__info-span {
    display: block;
    padding: 12px 17px;
    border-radius: 12px;
    background: var(--dark-color);
    color: var(--light-color);
    font-size: 16px;
    font-weight: 400;
    width: fit-content;
    margin-bottom: 20px;
  }

  &__info-text {
    margin: 0;
    color: var(--dark-color);
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 3;
  }

  &__tab {
    display: flex;
    align-items: stretch;
    gap: 10px;
    position: fixed;
    bottom: 10px;
    right: 50%;
    transform: translateX(50%);
    background: var(--light-color);
    width: calc(100% - 5px);
    padding: 8px;
    border-radius: 28px;
    z-index: 10;
  }

  &__tab-btn {
    width: 100%;
    min-height: 52px;
    background: var(--accent-color-2);
    border-radius: 16px;
    color: var(--light-color);
    font-size: 16px;
    font-weight: 400;
  }

  &__header{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    background: var(--light-color);
    z-index: 10;
  }
}

.seller {

  .swiper{
    height: 90vh;
  }

  .swiper-slide{
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__menu {
    display: flex;
    align-items: stretch;
    gap: 8px;
    margin-bottom: 40px;
  }

  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 52px;
    height: 52px;
    background: var(--accent-color);
    border-radius: 16px;
  }
}

.retailer-green-btn{
  background: #02D15C;
}
