.product {
    position: relative;
    min-height: 140px;
    border-radius: 5px;
    overflow: hidden;
    box-shadow:
        0 2px 2px 0 rgba(0, 0, 0, 0.08),
        0 0 2px 0 rgba(0, 0, 0, 0.04);
    &__inner {
        display: flex;
        align-items: stretch;
        gap: 8px;
    }
    &__picture {
        position: relative;
        width: 140px;
        height: 140px;
        overflow: hidden;
    }
    &__features {
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        bottom: 5px;
        right: 8px;
        background: #f7be00;
        width: 20px;
        height: 20px;
        border-radius: 50%;
    }
    &__description {
        padding-top: 8px;
    }
    &__title {
        display: block;
        color: var(--dark-color);
        font-size: 16px;
        line-height: 20px;
    }
    &__price {
        display: block;
        font-size: 16px;
        line-height: 22px;
        color: var(--dark-color);
        font-weight: 700;
    }
    &__owner {
        display: flex;
        align-items: center;
        gap: 8px;
        color: var(--dark-color);
        font-size: 13px;
        font-weight: 400;
    }
    &__owner-value {
        display: block;
        white-space: nowrap;
        max-width: 280px;
        overflow: hidden;
        text-overflow: ellipsis;
    }
    &__rating {
        display: flex;
        align-items: center;
        gap: 5px;
    }
    &__rating-value {
        font-size: 13px;
        font-weight: 400;
        color: var(--dark-color);
        line-height: 18px;
    }
    &__feedback::after {
        content: ")";
    }
    &__feedback::before {
        content: "(";
    }
    &__feedback {
        color: rgb(117, 117, 117);
    }
    &__location {
        display: flex;
        align-items: center;
        color: rgb(117, 117, 117);
        font-size: 13px;
        font-weight: 400;
        line-height: 18px;
    }
    &__delivery {
        display: block;
        color: rgb(117, 117, 117);
        font-size: 13px;
        font-weight: 400;
        line-height: 18px;
    }
    &__date {
        display: block;
        color: rgb(117, 117, 117);
        font-size: 13px;
        font-weight: 400;
        line-height: 18px;
    }
    &__btn {
        position: absolute;
        top: 10px;
        right: 10px;
    }

    &__preview {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      gap: 8px;
    }

    &__preview-item {
      display: flex;
      align-items: center;
      gap: 2px;
      font-size: 14px;
      font-weight: 400;
    }
}

.product-item {
    box-shadow:
    0 2px 2px 0 rgba(0, 0, 0, 0.08),
    0 0 2px 0 rgba(0, 0, 0, 0.04);
    max-width: 500px;
    width: 100%;
    margin: 0 auto;
    border-radius: 5px;
    padding: 0 10px 10px;
}
.product-item__picture {
    position: relative;
    max-height: 356px;
    max-width: 460px;
    margin-bottom: 10px;
    padding: 0 10px;
}
.swiper {
    max-width: 500px;
}
.swiper-slide .image {
    max-height: 356px;
    max-width: 500px;
}
.product-item__description {
    position: relative;
}
.product-item__title {
    display: block;
    color: rgb(33, 33, 33);
    font-size: 14px;
    line-height: 18px;
    margin-bottom: 5px;
}
.product-item__price {
    display: flex;
    align-items: center;
    gap: 5px;
    color: var(--dark-color);
    font-size: 14px;
    font-weight: 600;
    line-height: 19px;
}
.product-item__text {
    display: flex;
    align-items: center;
    gap: 5px;
    color: var(--dark-color);
    font-size: 11px;
    font-weight: 500;
    line-height: 20px;
}
.product-item__text span{
    display: flex;
    align-items: center;
    gap: 2px;
}
.product-item__span {
    display: block;
    font-size: 13px;
    font-weight: 400;
    color: rgb(117, 117, 117);
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}
.product-item__list {
    display: flex;
    align-items: center;
    gap: 5px;
    margin: 5px 0;
}
.product-item__item {
    padding: 5px;
    border-radius: 5px;
    background: #CCECFF;
    color: var(--dark-color);
    font-size: 11px;
    font-weight: 600;
}
.product-item__address {
    display: block;
    font-size: 13px;
    font-weight: 400;
    line-height: 30px;
    color: rgb(117, 117, 117);
}
.dote {
    display: inline-block;
    width: 7px;
    height: 7px;
    border-radius: 50%;
}
.product-item__delivery {
    display: block;
    font-size: 13px;
    font-weight: 400;
    line-height: 20px;
    color: rgb(117, 117, 117);
}
.product-item__date {
    display: block;
    font-size: 13px;
    font-weight: 400;
    line-height: 20px;
    color: rgb(117, 117, 117);
}
.product-item__like {
    position: absolute;
    top: 10px;
    right: 0;
    z-index: 2;
    width: 18px;
    height: 16px;
    background-image: url("../../img/like_blue.svg");
    background-size: 18px 16px;
    background-repeat: no-repeat;
}
.product-item__btn {
    width: 100%;
    height: 28px;
    border-radius: 6px;
    background: #02D15C;
    color: var(--light-color);
    font-size: 14px;
    font-weight: 400;
}
.arrow{
    display: inline-flex;
    align-items: stretch;
}
.product-item .arrow{
    position: absolute;
    bottom: 10px;
    left: 0;
    z-index: 2;
}

.arrow__text{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
    font-weight: 500;
}
