.tab {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    background: var(--light-color);
    border-top: .5px solid #EBEBEB;
    z-index: 7;

    &__inner {
        padding: 5px;
    }
    &__list {
        display: grid;
        grid-template-columns: repeat(5, 1fr);
    }
    &__item {
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &__link {
        display: flex;
        flex-direction: column;
        align-items: center;
        font-size: 10px;
        color: #CCCCCC;
    }
}

.active-link{

    & .tab__link{
        color: var(--dark-color);
    }
}