.control {

  &__inner {
    padding: 0 10px;
  }

  &__header-link{
    color: var(--accent-color-2);
    font-size: 15px;
    font-weight: 400;
    line-height: 20px;
  }

  &__title {
    margin: 0;
    color: var(--dark-color);
    font-size: 21px;
    font-weight: 700;
    line-height: 26px;
  }

  &__user {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }

  &__text {
    display: flex;
    align-items: center;
    gap: 5px;
    color: var(--dark-color);
    font-size: 15px;
    line-height: 20px;
    font-weight: 400;
    margin: 15px 0;
  }

  &__user-picture {
    position: relative;
    border-radius: 50%;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center center;

    .input-reset{
      width: 88px;
      height: 88px;
      opacity: 0;
    }

    &::before{
      content: '';
      position: absolute;
      bottom: 0;
      right: 0;
      width: 28px;
      height: 28px;
      background-image: url("../img/photo.svg");
      background-repeat: no-repeat;
      background-size: 18px 17px;
      background-position: 50% 50%;
      background-color: var(--light-color);
      border-radius: 50%;
      padding: 6px;
    }
  }

  &__pin {
    padding-top: 20px;
  }

  &__tab-title {
    display: block;
    color: var(--dark-color);
    font-size: 18px;
    font-weight: 600;
    line-height: 24px;
  }

  &__pin-phone {
    display: flex;
    align-items: center;
    gap: 5px;
    width: 100%;
    min-height: 44px;
    padding: 12px;
    border-radius: 6px;
    background: #E6F6FF;
  }

  &__phone-text {
    display: block;
    color: var(--dark-color);
    font-size: 13px;
    font-weight: 400;
    line-height: 16px;
  }

  &__pin-title {
    display: block;
    color: var(--dark-color);
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    margin-top: 15px;
  }

  &__pin-text {
    display: block;
    color: var(--grey-color);
    font-size: 13px;
    font-weight: 400;
    line-height: 18px;
  }

  &__container {
    margin-top: 20px;
  }

  &__vkText {
    margin: 15px 0 0;
    color: var(--dark-color);
    font-size: 14px;
    font-weight: 400;
    line-height: 22px;
  }

  &__container-text {
    margin: 0;
    color: var(--dark-color);
    font-size: 13px;
    font-weight: 400;
    line-height: 22px;
  }

  .tabs {

    &__nav {
      display: flex;
      align-items: center;
      gap: 20px;
      border-bottom: 2px solid #E3E2E1;
      padding-bottom: 16px;
      margin-top: 30px;
    }

    &__nav-btn {
      position: relative;
      font-weight: 700;
      color: var(--grey-color);
    }

    &__panel {
      padding-top: 20px;
    }
  }
}

.manage {

  &__article {
    background: #D4F0FF;
    padding: 20px 24px 36px;
    border-radius: 20px;
  }

  &__article-title {
    display: block;
    color: var(--dark-color);
    font-size: 22px;
    font-weight: 700;
    line-height: 26px;
    margin-bottom: 0;
  }

  &__article-text {
    color: var(--dark-color);
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    margin: 10px 0 0;
  }

  &__article-link {
    display: block;
    padding: 10px 17px;
    background: var(--light-color);
    border-radius: 6px;
    box-shadow: rgba(9, 94, 144, 0.2) 0px 4px 12px 0px, rgba(0, 122, 191, 0.1) 0px 2px 3px 0px;
    max-width: fit-content;
    color: var(--dark-color);
    font-size: 16px;
    font-weight: 400;
    margin-top: 10px;
  }

  &__hat {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
    gap: 20px;
  }

  &__hat-title {
    display: block;
    color: var(--dark-color);
    font-size: 18px;
    font-weight: 700;
    line-height: 22px;
    margin-bottom: 10px;
  }

  &__hat-text {
    color: var(--dark-color);
    font-size: 15px;
    font-weight: 500;
    line-height: 20px;
  }

  &__container {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    opacity: .5;
    padding-bottom: 50px;
  }

  &__item {
    margin-top: 30px;
  }

  &__title {
    display: block;
    color: var(--dark-color);
    font-size: 17px;
    font-weight: 700;
    line-height: 22px;
    margin-bottom: 10px;
  }

  &__text {
    display: block;
    color: var(--dark-color);
    font-size: 14px;
    font-weight: 500;
    line-height: 22px;
    margin-bottom: 20px;
  }

  &__avatar {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-width: 150px;
    min-height: 100px;
    max-width: 150px;
    max-height: 100px;
    background: #E3E2E1;
    border-radius: 5px;
    overflow: hidden;

    .input-reset{
      position: absolute;
      top: 0;
      left: 0;
      min-width: 150px;
      min-height: 100px;
      max-width: 150px;
      max-height: 100px;
      opacity: 0;
    }
  }

  &__btn {
    padding: 8px 10px;
    background: #E3E2E1;
    border-radius: 5px;
    font-size: 16px;
    font-weight: 400;
    color: var(--dark-color);
  }
}
