.rank {

  &__inner {
    padding-top: 10px;
  }

  &__content {
    display: flex;
    align-items: stretch;
    gap: 5px;
    margin-bottom: 20px;
  }

  &__title {
    color: var(--dark-color);
    font-size: 52px;
    font-weight: 700;
    line-height: 56px;
    margin: 0;
  }

  &__content-description {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }

  &__star {
    display: flex;
    align-items: center;
  }

  &__star-item {
    fill: #E0E0E0;
  }

  &__text {
    display: block;
    font-size: 13px;
    color: var(--dark-color);
    line-height: 16px;
  }

  &__scale {
    display: flex;
    align-items: stretch;
    gap: 10px;
  }

  &__scale-star {
    display: flex;
    flex-direction: column;
    gap: 3px;
  }

  &__scale-rating {
    display: flex;
    align-items: center;
  }

  &__scale-slide {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: 100%;
    padding: 5px 0;
    width: 100%;
  }

  &__slide {
    display: flex;
    align-items: center;
    width: 100%;
    height: 4px;
    background: #EBEBEB;
  }

  &__sidebar {
    display: flex;
    height: 4px;
    background: #7A7976;
  }

  &__scale-numbers {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    min-height: 100%;
  }

  &__num {
    display: block;
    font-size: 13px;
    color: var(--dark-color);
    line-height: 16px;
  }

  &__scale-title {
    color: var(--dark-color);
    font-size: 22px;
    font-weight: 700;
    line-height: 26px;
    margin: 30px 0 5px;
  }
}

.rank-header-btn{
  color: var(--grey-color);
  font-size: 16px;
  font-weight: 400;
}
