html {
    box-sizing: border-box;
}
*,
:after,
:before {
    box-sizing: inherit;
}
.disable-scroll {
    position: relative;
    overflow: hidden;
    height: 100vh;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
}
.graph-modal {
    --transition-time: 0.3s;
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 1000;
    background-color: rgba(0, 0, 0, 0.3);
    cursor: pointer;
    overflow-y: auto;
    overflow-x: hidden;
    text-align: center;
    opacity: 0;
    visibility: hidden;
    transition:
        opacity var(--transition-time),
        visibility var(--transition-time);
}
.graph-modal:before {
    content: "";
    display: inline-block;
    vertical-align: middle;
    height: 100%;
}
.graph-modal__container {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100dvw;
    margin: 0;
    display: inline-block;
    vertical-align: bottom;
    background-color: #fff;
    cursor: default;
    text-align: left;
    display: none;
    overflow-x: hidden;
    overflow-y: scroll;
    margin-top: -100%;
}
.graph-modal__close {
    position: absolute;
    right: 10px;
    top: 10px;
    border: none;
    padding: 0;
    height: 32px;
    width: 32px;
    z-index: 30;
    background-color: transparent;
    background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 512.001 512.001'%3E%3Cpath d='M284.286 256.002L506.143 34.144c7.811-7.811 7.811-20.475 0-28.285-7.811-7.81-20.475-7.811-28.285 0L256 227.717 34.143 5.859c-7.811-7.811-20.475-7.811-28.285 0-7.81 7.811-7.811 20.475 0 28.285l221.857 221.857L5.858 477.859c-7.811 7.811-7.811 20.475 0 28.285a19.938 19.938 0 0014.143 5.857 19.94 19.94 0 0014.143-5.857L256 284.287l221.857 221.857c3.905 3.905 9.024 5.857 14.143 5.857s10.237-1.952 14.143-5.857c7.811-7.811 7.811-20.475 0-28.285L284.286 256.002z'/%3E%3C/svg%3E");
    background-size: 16px;
    background-position: center center;
    background-repeat: no-repeat;
    cursor: pointer;
}
.graph-modal__content{
    position: relative;
}
.graph-modal.is-open {
    opacity: 1;
    visibility: visible;
    transition:
        opacity var(--transition-time),
        visibility var(--transition-time);
}
.graph-modal__container.graph-modal-open {
    display: inline-block;
}
.fade {
    opacity: 0;
}
.fade,
.fade.animate-open {
    transition: opacity var(--transition-time);
}
.fade.animate-open {
    opacity: 1;
    height: 100dvh;
}
.fadeInUp {
    position: relative;
    top: 100%;
    left: unset;
    opacity: 1;
    border-radius: 16px 16px 0 0;
    vertical-align: bottom;
    padding: 16px 6px;
    max-width: 540px;
    height: fit-content;
    transition: .2s linear;
}
.fadeInUp,
.fadeInUp.animate-open {
    transition:
        transform var(--transition-time),
        opacity var(--transition-time);
}
.fadeInUp.animate-open {
    opacity: 1;
    top: 0;
    transition: .2s linear;
}
.search__table {
    width: 100%;
    border-collapse: collapse;
}
.search__description{
    max-height: 740px;
    padding-bottom: 100px;
    overflow-y: scroll;

    &::-webkit-scrollbar{
        display: none;
    }
}
.search__row {
}
.search__col {
    height: 50px;

    & span{
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        font-size: var(--dark-color);
        font-size: 14px;
        line-height: 16px;
        font-weight: 400;
    }
}
.search__table-title {
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--dark-color);
    font-size: 16px;
    font-weight: 700;
    line-height: 22px;
    margin: 15px 0;
    text-align: center;
    width: 100%;
}
.search__table-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    background: transparent;
    width: 100%;
    height: 50px;
    color: var(--dark-color);
    font-size: 14px;
    font-weight: 400;
}
.not-date {
    text-decoration: line-through;
    opacity: .4;
}
.active-date {
    background: var(--accent-color-2) !important;
    color: var(--light-color) !important;
}

.search__nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
}
.search__nav-title {
    position: absolute;
    right: 50%;
    transform: translateX(50%);
    display: block;
    font-size: 16px;
    font-weight: 800;
    color: var(--dark-color);
    line-height: 22px;
    text-align: center;
}
.search__nav-btn {
    color: rgb(184, 184, 184);
    font-size: 16px;
    line-height: 22px;
    font-weight: 400;
}
.search__search {
    position: relative;
    height: 40px;
    width: 100%;
    overflow: hidden;
    margin-bottom: 40px;
}
.search__search-input {
    background: #F5F5F5;
    width: 100%;
    height: 40px;
    border-radius: 5px;
    padding: 0 50px 0 35px;
    background-image: url(../../img/search.svg);
    background-size: 20px;
    background-repeat: no-repeat;
    background-position-x: 6px;
    background-position-y: 50%;
    color: var(--dark-color);
    font-size: 16px;
    font-weight: 400;
}
.search__search-input::placeholder{
    color: #C1C1C1;
    font-size: 16px;
    font-weight: 400;
}
.search__search-btn {
    position: absolute;
    top: 50%;
    right: 22px;
    transform: translateY(-50%);
}
.search__content{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: stretch;
    gap: 10px;
}
.search__content-input{
    height: 44px;
    border-radius: 6px;
    transition: .2s linear;
    border: 3px solid transparent;
    padding: 0 0 0 20px;
}
.search__content-input:focus{
    transition: .2s linear;
    border-color: rgb(121, 210, 255);
}
.search__btnSubmit{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 44px;
    border-radius: 6px;
    background: var(--light-color);
    color: var(--dark-color);
    font-size: 16px;
    font-weight: 400;
    padding: 0 20px;
    box-shadow: rgba(9, 94, 144, 0.2) 0px 4px 12px 0px, rgba(0, 122, 191, 0.1) 0px 2px 3px 0px;
    margin: 20px 0;
}
.search__title {
    font-size: 16px;
    font-weight: 700;
    color: var(--dark-color);
    margin-bottom: 30px;
}
.search__list {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
}
.search__item {
    display: flex;
    align-items: center;
    gap: 5px;
    min-height: 46px;
}
.search__btn {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 10px;
    width: 100%;
    min-height: 46px;
}
.search__item-title {
    font-size: 16px;
    font-weight: 400;
    color: var(--dark-color);
}
.search__search{
    display: flex;
    align-items: center;
    gap: 10px;
}
.search__btn-svg{
    display: none;
}
.active-home{
    & span{
        color: rgba(0,0,0,0.5);
    }

    & svg{
        display: block !important;
    }
}
