.ads {

  &__inner{
    padding-top: 105px;
  }

  &__title {
    margin: 20px 0;
    color: var(--dark-color);
    font-size: 18px;
    font-weight: 700;
    line-height: 19px;
  }

  &__nav{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 10;
    background: var(--light-color);
    min-height: 105px;
    padding: 0 15px;
  }

  .tabs__nav{
    display: grid;
  }

  .tabs__nav-item{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 30px;
  }

  .tabs__content{
    padding-top: 0 !important;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: 140px;
    padding-top: 30px;
    gap: 5px;
  }

  .tabs__nav-btn--active {
    color: var(--accent-color-2) !important;

    &::after{
        content: '';
        position: absolute;
        bottom: -18px;
        left: 0;
        width: 100%;
        height: 3px;
        background: var(--accent-color-2);
    }
}

  &__btn {
    position: fixed;
    bottom: 70px;
    right: 50%;
    transform: translateX(50%);
    display: flex;
    align-items: center;
    background: var(--accent-color-2);
    border-radius: 24px;
    min-height: 50px;
    padding: 10px 12px;
    color: var(--light-color);
    font-size: 12px;
    font-weight: 700;
    line-height: 16.5px;
    text-transform: uppercase;
    min-width: 220px;
  }

  &__btn-svg {
    margin-right: 10px;
    min-height: 24px;
    min-width: 24px;
  }
}
