.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 72px;
    z-index: 20;
    background: var(--light-color);
}
.header__inner {
    position: relative;
    z-index: 15;
    display: flex;
    align-items: center;
    padding: 8px 0 12px;
    background: var(--light-color);
}
.header__search {
    display: flex;
    align-items: center;
    border-radius: 16px;
    background: var(--accent-color);
    overflow: hidden;
    padding-right: 20px;
    margin-right: 15px;
    width: 100%;
}
.header__search-input {
    width: 100%;
    height: 52px;
    overflow: hidden;
    background: inherit;
    padding: 0 10px 0 45px;
    background: url("../img/logo.svg");
    background-size: 20px 20px;
    background-position-y: 50%;
    background-position-x: 15px;
    background-repeat: no-repeat;
}
.header__search-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 34px;
    width: 34px;
}
.header__menu {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 15;
    width: 100%;
    height: 100%;
}
.header__menu-bg {
    position: fixed;
    bottom: 0;
    left: 0;
    z-index: 9;
    width: 100%;
    height: 50%;
    background: rgba(0, 0, 0, 0.5);
}
.header__search-link {
    color: rgb(0, 153, 247);
    font-size: 16px;
    white-space: nowrap;
}

.header {
    &__content {
        position: absolute;
        top: 72px;
        left: 0;
        width: 100dvw;
        height: fit-content;
        z-index: 15;
        background: var(--light-color);
        max-height: 80%;
        height: 100%;
    }
    &__menu-icon {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 41px;
        height: 20px;
    }
    &__menu-item {
        position: relative;
        display: flex;
        align-items: center;
        padding: 6px 11px 14px 12px;
        min-height: 60px;

        &:not(:last-child) {
            &::after {
                content: "";
                position: absolute;
                bottom: 0;
                right: 0;
                background: #ebebeb;
                height: 1px;
                width: calc(100% - 65px);
            }
        }
    }
    &__menu-description {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        padding-left: 12px;
    }
    &__menu-content {
        display: flex;
        flex-direction: column;
    }
    &__menu-title {
        display: block;
        color: var(--dark-color);
        font-size: 16px;
        line-height: 20px;
    }
    &__menu-text {
        display: block;
        color: rgb(117, 117, 117);
        font-size: 13px;
        line-height: 16px;
    }
    &__menu-arrow {
        transform: rotate(90deg);
    }

    &__fixed {
        position: relative;
        bottom: 0;
        z-index: -1;
        background: var(--light-color);
        width: 100%;
        transition: 0.5s ease-in-out;
        padding: 0 0 15px 15px;
    }
    &__fixed-list {
        display: flex;
        align-items: center;
        gap: 5px;
    }
    &__fixed-link {
        display: flex;
        align-items: center;
        gap: 2px;
        white-space: nowrap;
        padding: 0 12px;
        height: 36px;
        border-radius: 12px;
        color: var(--dark-color);
        font-size: 13px;
        line-height: 36px;
        background: #f5f5f5;
        min-width: max-content;
    }

    &__fixed-link span{
        display: flex;
        align-items: center;
        gap: 2px;
    }

    &__fixed-title{
        color: var(--dark-color);
        font-size: 18px;
        font-weight: 700;
        line-height: 22px;
        margin: 0;
    }
}

.active-fixed-block {
    position: relative !important;
    bottom: 200px !important;
    transition: 0.5s ease-in-out;
}
.filter {
    padding: 80px 24px 88px;
    background: var(--light-color);
    &__nav {
        position: fixed;
        top: 0;
        left: 0;
        display: flex;
        align-items: center;
        justify-content: space-between;
        background: var(--light-color);
        height: 56px;
        width: 100%;
        padding: 24px 24px 0;
    }
    &-active-item {
        background: #dbd3c1 !important;
    }
    &__search {
      display: flex;
      align-items: center;
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      min-height: 56px;
      background: var(--light-color);
      gap: 20px;
      padding: 0 20px;
      box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 4px 0px;

      .input-reset{
        width: 100%;
      }
    }

    &__region-list {
      display: grid;
      grid-template-columns: repeat(1, 1fr);
      gap: 30px;
    }
    &__region-btn {
      font-size: 16px;
      font-weight: 500;
      color: var(--dark-color);
    }
    &__container {
        display: flex;
        flex-direction: column;
        gap: 25px;
    }
    &__contentItem {
        width: 100%;
    }
    &__title {
        display: block;
        font-size: 16px;
        color: var(--dark-color);
        font-weight: 900;
    }
    &__btn {
        display: inline-block;
        color: rgb(0, 170, 255);
        font-size: 16px;
        font-weight: 400;
    }
    &__allTitle {
        font-size: 22px;
        font-weight: 600;
        color: var(--dark-color);
        line-height: 28px;
        margin-bottom: 12px;
    }
    &__list {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        gap: 8px;
    }
    &__list-btn {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0 15px;
        height: 34px;
        border-radius: 5px;
        background: var(--accent-color);
        color: var(--dark-color);
    }
    &__sectionTitle {
        display: block;
        color: var(--dark-color);
        font-size: 16px;
        font-weight: 700;
        margin: 0 0 10px;
    }
    &__input {
        width: 100%;
        height: 44px;
        border-radius: 5px;
        background: var(--accent-color);
    }
    &__label {
        display: flex;
        align-items: center;
        margin: 10px 0;
        font-size: 16px;
        font-weight: 500;
        color: var(--dark-color);
    }
    &__checkbox {
        position: absolute;
        z-index: -1;
        opacity: 0;

        & + label {
            display: inline-flex;
            align-items: center;
            user-select: none;
        }

        & + label::before {
            content: "";
            display: inline-block;
            width: 18px;
            height: 18px;
            flex-shrink: 0;
            flex-grow: 0;
            background: #ebeae8;
            border-radius: 2px;
            margin-right: 0.5em;
            background-repeat: no-repeat;
            background-position: center center;
            background-size: 50% 50%;
            transition: 0.2s linear;
        }

        &:checked + label::before {
            background-color: var(--dark-color);
            background-image: url("../img/check.svg");
            background-size: 12px 10px;
            transition: 0.2s linear;
        }

        &:disabled + label::before {
            opacity: 0.5;
        }

        &:not(:disabled):not(:checked) + label:hover::before {
            border-color: #b3d7ff;
        }
    }
    &__radio {
        position: absolute;
        z-index: -1;
        opacity: 0;

        & + label {
            display: inline-flex;
            align-items: center;
            user-select: none;
        }

        & + label::before {
            content: "";
            display: inline-block;
            width: 21px;
            height: 21px;
            flex-shrink: 0;
            flex-grow: 0;
            background: #ebeae8;
            border-radius: 50%;
            margin-right: 0.5em;
            background-repeat: no-repeat;
            background-position: center center;
            background-size: 45% 45%;
        }

        &:checked + label::before {
            background-color: var(--dark-color);
            background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'%3e%3ccircle r='3' fill='%23fff'/%3e%3c/svg%3e");
            background-position: center center;
        }

        &:disabled + label::before {
            opacity: 0.5;
        }
    }
    &__content {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 10px;
        grid-template-rows: 44px;
    }
    &__price {
        background: var(--accent-color);
        border-radius: 5px;
        padding: 0 15px;
    }
    &__seller {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: 44px;
        margin-bottom: 50px;
        border-radius: 5px;
        overflow: hidden;
    }
    &__seller-item {
        position: relative;
        display: flex;
        align-items: center;
        justify-content: center;
        background: var(--accent-color);

        &:not(:last-child) {
            &::after {
                content: "";
                position: absolute;
                right: 0;
                top: 50%;
                transform: translateY(-50%);
                width: 1px;
                height: 20px;
                background: #e0e0e0;
            }
        }
    }

    &__label {
        position: relative;
    }

    &__label svg {
        position: absolute;
        right: 15px;
        padding: 6px;
        transform: rotate(180deg);
    }

    &__label-value {
        position: absolute;
        left: 15px;
        font-size: 16px;
        font-weight: 400;
        line-height: 24px;
        color: var(--dark-color);
    }

    &__label-fragment {
        opacity: 0.7;
    }

    &__result {
        display: flex;
        align-items: center;
        justify-content: center;
        background: rgb(0, 153, 247);
        color: var(--light-color);
        font-size: 16px;
        font-weight: 400;
        line-height: 24px;
        position: fixed;
        bottom: 24px;
        right: 50%;
        transform: translateX(50%);
        width: calc(100dvw - 48px);
        height: 44px;
        border-radius: 5px;
    }
}
.mb-15{
    margin-bottom: 15px !important;
}
.filter-close-btn {
    position: unset !important;
}
.location-close-btn{
    position: unset !important;
    background-image: url("../img/arrow.svg") !important;
    background-repeat: no-repeat;
    transform: rotate(-90deg);
    width: 24px !important;
    height: 24px !important;
}
.header-return-btn{
    margin-right: 15px;
}

.active-nav{
    background: var(--dark-color);
}
.active-nav span{
    color: var(--light-color) !important;
}
.active-nav svg{
    fill: var(--light-color);
}

.header__fixed-content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 15px;
    gap: 10px;
}
.header__fixed-btn {
    display: flex;
    align-items: center;
    gap: 5px;
    font-size: 13px;
}
.header__fixed-btn span span{
    color: var(--dark-color);
}
.header__fixed-btn span{
    color: var(--grey-color);
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    text-align: left;
}

.mt-15{
    margin-top: 15px;
}

.mt-200{
    margin-top: 200px;
}

.mt-120{
    margin-top: 120px;
}

.filter {

  &__radius-search {
    display: flex;
    align-items: center;
    gap: 20px;
    padding: 0 20px 0 0;
    background: var(--accent-color);
    width: calc(100% - 50px);
    margin: 50px auto 0;
    border-radius: 6px;
    overflow: hidden;


    .input-reset{
      background: var(--accent-color);
      min-height: 36px;
      width: 100%;
      padding-left: 50px;
      background-image: url(../img/search.svg);
      background-size: 20px;
      background-repeat: no-repeat;
      background-position-y: 50%;
      background-position-x: 15px;
    }
  }
}
.radius {

  &__list {
    display: flex;
    align-items: center;
    gap: 5px;
    position: fixed;
    bottom: 85px;
    left: 0;
    background: var(--light-color);
    padding: 0 25px;
  }

  &__btn {
    padding: 8px 16px;
    background: var(--accent-color);
    border-radius: 6px;
    font-size: 18px;
    font-weight: 500;
  }
}
