.feedback {
    background: var(--light-color);
    height: 100dvh;

    &__wrapper{
        background: #BFBFBF;
        max-width: 540px;
        margin: 0 auto;
        padding-top: 15px;
    }

    .tabs__content{
      padding-top: 0 !important;
    }

    .tabs__nav{
      position: unset !important;
    }

    &__btn{
        display: flex;
        align-items: center;
        justify-content: center;
        position: fixed;
        bottom: 15px;
        right: 50%;
        transform: translateX(50%);
        width: calc(100% - 40px);
        min-height: 46px;
        border-radius: 5px;
        background: var(--accent-color-2);
        color: var(--light-color);
        font-size: 16px;
        font-weight: 400;
    }

    &__inner {
        position: relative;
        background: var(--light-color);
        min-height: calc(100vh - 10px);
        border-top-left-radius: 14px;
        border-top-right-radius: 14px;
        overflow: hidden;
        padding: 0 20px;
    }

    &__header {
    }

    &__header-content {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 20px 0;
        gap: 20px;
    }

    &__header-back {

        & .image{
            transform: rotate(-90deg);
            min-width: 18px;
            min-height: 18px;
        }
    }

    &__title {
        font-size: 18px;
        font-weight: 700;
        color: var(--dark-color);
        margin: 0;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
    }

    &__header-btn {

        & .image{
            min-width: 18px;
            min-height: 18px;
        }
    }

    &__item {
    }


    &__header-top {
        padding-top: 20px;
    }

    &__path {
        display: flex;
        align-items: center;
        gap: 2px;
        margin-bottom: 10px;
    }

    &__path-link {
        color: var(--grey-color);
        font-size: 13px;
        font-weight: 400;
    }

    &__header-title {
        margin: 15px 0 30px;
        color: var(--dark-color);
        font-size: 26px;
        font-weight: 700;
        line-height: 30px;
    }


    .tabs {

        &__nav {
            display: flex;
            align-items: center;
            gap: 15px;
            border-bottom: 3px solid #E3E2E1;
            padding-bottom: 15px;
        }

        &__nav-item {
        }

        &__nav-btn {
            position: relative;
            color: var(--grey-color);
            font-size: 16px;
            font-weight: 600;
            line-height: 24px;
        }

        &__content {
        }

        &__panel {
            background: var(--light-color);
            overflow-y: scroll;
            max-height: 100dvh;
            padding-top: 10px;
            padding-bottom: 220px;

            &::-webkit-scrollbar{
                display: none;
            }
        }
    }
}
.rating {

    &__text {
        color: rgb(48, 168, 0);
        font-size: 16px;
        font-weight: 400;
        line-height: 22px;
        margin: 0 0 10px;
    }

    &__text-span {
        padding: 2px 8px;
        border-radius: 3px;
        color: var(--light-color);
        background: rgb(48, 168, 0);
    }

    &__panel {
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        gap: 5px;
    }

    &__panel-item {
        display: flex;
        align-items: center;
        gap: 5px;
    }

    &__panel-content {
        display: flex;
        align-items: center;
    }

    &__star {
        width: 16px;
        height: 16px;
        fill: #FFB021;
    }

    &__panel-progress {
        width: calc(100% - 50px);
        height: 4px;
        border-radius: 3px;
        background: #EBEAE8;
        overflow: hidden;
    }

    &__panel-progress-bar {
        height: 4px;
        background: #7A7976;
        border-radius: 3px;
    }

    &__panel-text {
        display: block;
        color: var(--dark-color);
        font-size: 14px;
        font-weight: 400;
        min-width: 30px;
        margin-left: 5px;
        text-align: right;
    }
}
.disabled-star {
    fill: #E3E2E1 !important;
}
.comment {
    margin-top: 30px;

    &__user {
        display: flex;
        align-items: flex-end;
        gap: 8px;

        & .image{
            border-radius: 50%;
            min-width: 32px;
            min-height: 32px;
        }
    }

    &__experience{
        display: block;
        color: var(--grey-color);
        font-size: 15px;
        line-height: 22px;
        margin: 5px 0;
    }

    &__user-title {
        display: block;
        color: var(--dark-color);
        font-size: 16px;
        font-weight: 600;
        line-height: 22px;
    }

    &__user-date {
        display: block;
        color: var(--grey-color);
        font-size: 13px;
        font-weight: 400;
        line-height: 16px;
    }

    &__feedback {
        display: flex;
        align-items: center;
        margin-bottom: 10px;
    }

    &__slide {
        display: flex;
        align-items: flex-start;
        gap: 1px;
        overflow: hidden;
        border-radius: 12px;
        width: fit-content;
    }

    &__description {
        padding-top: 30px;
    }

    &__description-title {
        color: var(--dark-color);
        font-size: 18px;
        font-weight: 700;
        margin-top: 0;
    }

    &__description-text {
        color: var(--dark-color);
        font-size: 14px;
        font-weight: 400;
        margin: 0 0 20px;
    }
}

.property {

    &__title {
        display: block;
        color: var(--dark-color);
        font-size: 16px;
        font-weight: 700;
        line-height: 20px;
        margin: 0 0 8px;
    }

    &__text {
        margin: 0;
        font-size: 15px;
        font-weight: 400;
        line-height: 25px;
        color: var(--grey-color);
    }

    &__text-span {
        color: var(--dark-color);
    }

    &__advice {
        font-size: 11px;
        line-height: 14px;
        color: var(--grey-color);
        font-weight: 600;
        margin: 0 0 20px;
    }

    &__titleMarket{
        color: var(--dark-color);
        font-size: 26px;
        font-weight: 700;
        margin: 0 0 20px;
        line-height: 30px;
    }

    &__btn {
        display: block;
        width: fit-content;
        margin-top: 20px;
        padding: 17px;
        border-radius: 12px;
        background: var(--accent-color);
        color: var(--dark-color);
        font-size: 16px;
        font-weight: 400;
    }
}

.model {

    &__title {
        color: var(--dark-color);
        font-size: 26px;
        font-weight: 700;
        line-height: 30px;
        margin-bottom: 10px;
    }

    &__options {
        margin-top: 20px;
    }

    &__options-title {
        display: block;
        color: var(--dark-color);
        font-size: 16px;
        font-weight: 700;
        line-height: 20px;
        margin-bottom: 5px;
    }

    &__options-content {
        display: flex;
        align-items: stretch;
        gap: 5px;
    }

    &__options-btn {
        padding: 12px 17px;
        background: var(--accent-color);
        border-radius: 12px;
        font-size: 16px;
        font-weight: 400;
        color: var(--dark-color);
    }

    &__options-color {
        position: relative;
        width: 32px;
        height: 32px;
        border-radius: 50%;
        border: 1px solid #9C9C9C;
        margin-right: 5px;
    }

    &__property {
    }

    &__btn{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        min-height: 52px;
        border-radius: 16px;
        background: var(--dark-color);
        color: var(--light-color);
        font-size: 16px;
        font-weight: 400;
        padding: 5px 20px;
        margin: 20px 0 40px;
    }

    &__property-title {
        color: var(--dark-color);
        font-size: 26px;
        font-weight: 700;
        line-height: 30px;
        margin: 0 0 15px;
    }

    &__property-text {
        display: block;
        color: var(--grey-color);
        font-size: 15px;
        font-weight: 400;
        line-height: 22px;
        margin-bottom: 10px;
    }

    &__property-btn {
        padding: 12px 17px;
        background: var(--accent-color);
        border-radius: 12px;
        font-size: 16px;
        font-weight: 400;
        color: var(--dark-color);
        margin-bottom: 40px;
    }
}

.model-fixed-btn{
    position: fixed;
    bottom: -20px;
    right: 50%;
    transform: translateX(50%);
    width: calc(100% - 70px);
    z-index: 10;
}

.model-active-btn{
    background: var(--dark-color);
    color: var(--light-color);
}

.model-active-color{
    &::before{
        content: '';
        position: absolute;
        right: 50%;
        top: 50%;
        transform: translate(50%, -50%);
        width: 40px;
        height: 40px;
        border: 2px solid var(--dark-color);
        border-radius: 50%;
    }
}
