.stories {
    &__inner {
        display: flex;
        align-items: center;
        gap: 15px;
        padding-top: 35px;
    }
    &__banner {
        border-radius: 16px;
        min-width: 192px;
        min-height: 128px;
    }
    &__list {
        display: flex;
        align-items: center;
        gap: 15px;
    }
    &__item {
        display: flex;
        align-items: center;
        justify-content: center;

        & .image{
            border-radius: 50%;
            min-width: 92px;
            min-height: 92px;
            padding: 2px;
        }
    }
    &__btn {
        display: flex;
        flex-direction: column;
        align-items: center;
        font-size: 13px;
        line-height: 16px;
        text-align: center;
        color: #000;
    }

    &__nav {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 10px;
    }
    &__title {
        display: block;
        color: var(--light-color);
        font-size: 47px;
        font-weight: 700;
        line-height: 51px;
        margin: 0;
    }
    &__text {
        display: block;
        color: var(--light-color);
        font-size: 23px;
        line-height: 27px;
        margin: 15px 0 0 0;
    }
    &__link {
        display: flex;
        align-items: center;
        width: fit-content;
        border-radius: 5px;
        color: var(--light-color);
        background: rgb(0, 153, 247);
        padding: 15px 19px;
        margin-top: 30px;
        font-size: 23px;
    }
}
.viewed-stories {
    border: 2px solid #FF6567;
}

.stories-modal {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 20px;
    height: 100dvh;
    width: 100dvw;
    background-size: cover;
}
.stories-modal-container{
    overflow: hidden;
    border-radius: 20px;
    & .graph-modal__close{
        position: absolute;
        top: 35px;
        right: 20px;
    }
}
.stories-slide {
    display: block;
    height: 2px;
    background: #B7B8C1;
    border-radius: 10px;
}

