.messenger {

  &__inner {
    padding-top: 130px;
  }

  &__header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    padding: 15px;
    z-index: 10;
    background: var(--light-color);
  }

  &__header-top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    margin-bottom: 20px;
  }

  &__header-search {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    padding-right: 10px;
    background: #F2F1F0;
    width: 100%;
    border-radius: 6px;
    overflow: hidden;
  }

  &__header-input {
    height: 28px;
    width: 100%;
    font-size: 13px;
    padding-left: 40px;
    background: inherit;
    background-image: url(../img/search.svg);
    background-size: 16px;
    background-repeat: no-repeat;
    background-position-x: 10px;
    background-position-y: 50%;

    &::placeholder{
      font-size: 13px;
    }
  }

  &__filter {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 10px;
    font-size: 14px;
    font-weight: 500;
    width: 100%;
    min-height: 32px;
    border: 1px solid #CCCCCC;

    .image{
      rotate: 180deg;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 20px;
  }

  &__item {
    position: relative;
    display: flex;
    align-items: flex-start;
    gap: 20px;
    background: var(--light-color);
    padding-top: 10px;
    padding-bottom: 10px;
  }

  &__content{
    max-width: 300px;
    width: 100%;
  }

  &__title {
    display: block;
    font-size: 16px;
    font-weight: 700;
    line-height: 24px;
    color: var(--dark-color);
  }

  &__text {
    display: block;
    font-size: 14px;
    font-weight: 500;
    line-height: 14px;
    color: var(--dark-color);
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }

  &__date{
    position: absolute;
    right: 0;
    display: block;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    color: var(--grey-color);
  }
}
.admin {
  background: #E6F6FF;
  border-radius: 20px;
  padding: 10px 40px;
}

@media (max-width: 400px){
  .messenger__content{
    max-width: 200px;
  }
}
