.profile {

  .main{
    display: flex;
    flex-direction: column;
    gap: 8px;
    background: #F4F4F4;
    min-height: 100dvh;
    padding: 56px 0 65px;
  }

  &__description{
    position: relative;
    display: flex;
    align-items: center;
    padding-right: 140px;
    padding-bottom: 20px;

    & .profile__text{
      text-align: left;
    }

    & .image{
      position: absolute;
      right: 0;
    }
  }

  .p-header{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 56px;
    background: var(--light-color);
    z-index: 10;
  }

  .new-profile-item{
    position: relative;

    &::after{
      content: 'Новое';
      color: var(--light-color);
      font-size: 11px;
      font-weight: 500;
      background: #FF6163;
      border-radius: 8px;
      padding: 1px 6px;
      line-height: 19px;
      margin-left: 5px;
    }
  }

  &__section {
    background: var(--light-color);
    -webkit-box-shadow: rgba(0, 0, 0, 0.08) 0px 2px 2px 0px;
    box-shadow: rgba(0, 0, 0, 0.08) 0px 2px 2px 0px;
  }

  &__inner {
    padding: 20px 15px;
  }

  &__title {
    display: block;
    color: var(--dark-color);
    font-size: 16px;
    font-weight: 700;
    line-height: 19px;
    margin-bottom: 20px;
  }

  &__content {
    display: flex;
    align-items: center;
    min-height: 60px;
    padding: 10px 0;
  }

  &__content-text {
    color: var(--dark-color);
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    margin-left: 32px;
  }

  &__menu {
    position: relative;
    padding-top: 20px;

    &::before{
      content: '';
      position: absolute;
      top: 0;
      left: -15px;
      width: calc(100% + 30px);
      height: 1px;
      background: #E0E0E0;
    }
  }

  &__link {
    display: block;
    color: var(--accent-color-2);
    font-size: 12px;
    font-weight: 700;
    line-height: 16px;
    text-transform: uppercase;
  }

  &__more {
    background: var(--light-color);
    -webkit-box-shadow: rgba(0, 0, 0, 0.08) 0px 2px 2px 0px;
    box-shadow: rgba(0, 0, 0, 0.08) 0px 2px 2px 0px;
    min-height: 72px;
    padding: 16px;
  }

  &__more-link {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 5px;
  }

  &__more-title {
    display: block;
    color: var(--dark-color);
    font-size: 16px;
    font-weight: 400;
    line-height: 19px;
  }

  &__more-text {
    display: block;
    color: var(--grey-color);
    font-size: 14px;
    font-weight: 400;
    line-height: 17px;
  }

  &__nav-inner {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }

  &__nav-link {
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--light-color);
    -webkit-box-shadow: rgba(0, 0, 0, 0.08) 0px 2px 2px 0px;
    box-shadow: rgba(0, 0, 0, 0.08) 0px 2px 2px 0px;
    min-height: 56px;
    padding: 20px 16px;
    color: var(--accent-color-2);
    font-size: 12px;
    font-weight: 700;
    line-height: 16px;
    text-transform: uppercase;
  }
}
