.footer {
    background: #EEEEEE;
    box-shadow: inset 0 2px 2px 0 rgba(0,0,0,.08), inset 0 0 2px 0 rgba(0,0,0,.04);
    height: 200px;
    width: 100%;
    padding-top: 24px;
    margin-top: 30px;
}
.footer__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.footer__list {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap: 10px;
    margin-bottom: 20px;
}
.footer__link {
    font-size: 12px;
    font-weight: 400;
    color: var(--accent-color-2);
}
.footer__text {
    color: #bdbdbd;
    font-size: 13px;
    font-weight: 400;
}
.footer__content {
    display: flex;
    align-items: center;
    gap: 10px;
}
