.notification {

  &__header{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 56px;
    background: var(--light-color);
    z-index: 10;
    box-shadow: rgba(0, 0, 0, 0.08) 0px 2px 2px 0px, rgba(0, 0, 0, 0.04) 0px 0px 2px 0px;

    .p-header__btn{
      display: flex;
      align-items: center;
      gap: 30px;
      font-size: 18px;
      font-weight: 700;
      line-height: 19px;
      color: var(--dark-color);
    }
  }

  &__list {
    padding-top: 70px;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 20px;
  }

  &__content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 10px;
  }

  &__title {
    display: block;
    font-size: 16px;
    font-weight: 700;
    color: var(--dark-color);
    line-height: 22px;
    text-align: left;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &__date {
    display: block;
    font-size: 13px;
    font-weight: 400;
    color: var(--grey-color);
    line-height: 22px;
    text-align: right;
    white-space: nowrap;
  }

  &__text {
    margin: 5px 0 0;
    line-height: 22px;
    font-size: 14px;
    font-weight: 400;
    color: #272727;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 2;
  }
}
