.slide {
    &__inner {
        padding: 30px 0;
    }
    &__list {
        display: flex;
        align-items: stretch;
        gap: 10px;
    }
    &__item {
        width: 100%;
        min-width: 500px;
        max-width: 500px;
    }
    &__item .image {
        border-radius: 16px;
        width: 100%;
        height: 140px;
        overflow: hidden;
        margin-bottom: 5px;
        min-width: 300px;
    }
    &__title {
        display: block;
        color: var(--dark-color);
        font-size: 26px;
        font-weight: 600;
        line-height: 30px;
        margin: 0 7px 16px;
    }
    &__products {
        display: flex;
        align-items: center;
        gap: 8px;
        flex-wrap: wrap;
    }
    &__link {
        display: block;
        padding: 9px 15px;
        background: var(--accent-color);
        border-radius: 12px;
        font-size: 13px;
        color: var(--dark-color);
    }
}
