.cart {

  &__nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 0;
    margin-top: 60px;
  }

  &__link {
    color: var(--accent-color-2);
    font-size: 15px;
    font-weight: 400;
    line-height: 20px;

    & span{
      &::before{
        content: '(';
      }
      &::after{
        content: ')';
      }
    }
  }

  &__page{
    .p-header{
      position: fixed;
      top: 0;
      left: 0;
      width: 100dvw;
      background: var(--light-color);
      z-index: 10;
    }
  }

  &__header-title{
    display: block;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    font-size: 16px;
    font-weight: 700;
    color: var(--dark-color);
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 20px;
    padding-bottom: 160px;
  }

  &__user {
    display: flex;
    align-items: center;
    gap: 5px;
    margin-bottom: 10px;
  }

  &__user-content {
    display: flex;
    align-items: center;
    gap: 5px;
  }

  &__user-name {
    display: block;
    color: var(--dark-color);
    font-size: 21px;
    font-weight: 700;
    line-height: 21px;
  }

  &__user-star {
    fill: #FFA500;
  }

  &__user-rating {
    display: block;
    color: var(--dark-color);
    font-size: 15px;
    font-weight: 400;
    line-height: 22px;
  }

  &__user-num {
    display: block;
    font-size: 15px;
    font-weight: 400;
    color: var(--grey-color);

    &::before{
      content: '(';
    }
    &::after{
      content: ')';
    }
  }

  &__catalog {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 15px;
  }

  &__image{
    min-width: 96px;
    min-height: 96px;
    border-radius: 12px;
  }

  &__product-picture{
    margin-right: 15px;
  }

  &__product {
    display: flex;
    align-items: stretch;
  }

  &__product-description {
    display: flex;
    flex-direction: column;
    margin-right: 10px;
  }

  &__price {
    display: block;
    color: var(--dark-color);
    font-size: 18px;
    font-weight: 700;
    line-height: 22px;
  }

  &__title {
    display: block;
    color: var(--dark-color);
    font-size: 13px;
    font-weight: 400;
    line-height: 16px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  &__quantity {
    display: flex;
    align-items: stretch;
    margin-top: 8px;
    background: #F2F1F0;
    max-width: fit-content;
    border-radius: 12px;
  }

  &__quantity-btn {
    min-height: 44px;
    min-width: 44px;
  }

  &__quantity-num {
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 36px;
    min-height: 46px;
    color: var(--dark-color);
    font-size: 16px;
    font-weight: 400;
  }

  &__catalog-link {
    display: block;
    color: rgb(161, 104, 247);
    font-size: 13px;
    font-weight: 400;
    line-height: 18px;
    margin-top: 10px;
  }

  &__catalog-list {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-left: auto;
    gap: 10px;
  }

  &__catalog-btn {
    min-width: 20px;
    min-height: 20px;
  }

  &__tab {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100dvw;
    background: var(--light-color);
    padding: 15px;
  }

  &__tab-content {
    display: flex;
    align-items: flex-end;
    gap: 5px;
  }

  &__tab-text {
    display: block;
    color: var(--dark-color);
    font-size: 15px;
    font-weight: 400;
    line-height: 22px;
    white-space: nowrap;
  }

  &__tab-border {
    width: 100%;
    height: 0;
    border-top: 1px dotted rgb(184, 184, 184);
    border-bottom: 1px dotted rgb(184, 184, 184);
    margin-bottom: 5px;
  }

  &__tab-title {
    display: block;
    color: var(--dark-color);
    font-size: 18px;
    font-weight: 700;
    line-height: 22px;
    margin-top: 10px;
    white-space: nowrap;
  }

  &__tab-btn {
    min-height: 52px;
    padding: 0 20px;
    width: 100%;
    background: rgb(161, 104, 247);
    border-radius: 16px;
    color: var(--light-color);
    font-size: 16px;
    font-weight: 400;
    margin-top: 20px;
  }
}
