.p-header {

    &__inner {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 10px;
        min-height: 56px;
    }

    &__title {
      display: block;
      color: var(--dark-color);
      font-size: 18px;
      line-height: 19px;
      font-weight: 700;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      line-height: 22px;
    }

    &__content {
        display: flex;
        align-items: center;
        gap: 10px;
    }
}

