.shop {

    &__inner {
        padding-top: 10px;
    }

    &__preview {
        display: flex;
        align-items: center;
        gap: 5px;

        & .image{
            min-width: 177px;
            border-radius: 5px;
        }
    }

    &__description {
        margin-top: 20px;
    }

    &__description-text {
        margin: 0 0 20px;
        color: var(--dark-color);
        font-size: 16px;
        font-weight: 700;
        line-height: 22px;
    }

    &__price {
        display: block;
        color: var(--dark-color);
        font-size: 32px;
        font-weight: 700;
        line-height: 36px;
        margin: 0;
    }

    &__description-title {
        font-size: 18px;
        margin: 0;
        font-weight: 700;
        line-height: 22px;
        color: rgb(117, 117, 117);
    }

    &__description-btn {
        position: relative;
        display: flex;
        align-items: center;
        gap: 10px;
        padding: 10px;
        background: #E2FFD6;
        border-radius: 16px;
        width: 100%;
        margin-top: 30px;

        &::after{
            content: '';
            position: absolute;
            top: 50%;
            right: 10px;
            transform: translateY(-50%);
            width: 18px;
            height: 18px;
            background-image: url(../img/arrow_90.svg);
            background-size: cover;
            background-repeat: no-repeat;
        }
    }

    &__description-btn-title {
        display: block;
        text-align: left;
        color: var(--dark-color);
        font-size: 16px;
        font-weight: 700;
        line-height: 20px;
    }

    &__description-btn-text {
        display: block;
        text-align: left;
        color: var(--dark-color);
        font-size: 13px;
        font-weight: 500;
        line-height: 16px;
    }

    &__link{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        height: 28px;
        background: #00AAFF;
        color: var(--light-color);
        font-size: 16px;
        font-weight: 400;
        border-radius: 5px;
        margin-top: 50px;
    }

    &__rating {
        padding: 0 10px;
    }

    &__rating-nav {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__rating-title {
        display: block;
        margin: 0;
        color: var(--dark-color);
        font-size: 22px;
        font-weight: 800;
        line-height: 28px;
        margin-bottom: 10px;
    }

    &__rating-text {
        margin: 0;
        color: var(--dark-color);
        font-size: 16px;
        font-weight: 400;
    }
}
